@import "utils";
@import "fonts";
@import "typography";

body {
  margin: 0;
}

.host_card {
  width: 491px;
  margin-left: 270px;
  margin-bottom: 75px;
  color: #3d4e61;
    &__hostName {
      font-size: 28px;
      line-height: 33px;
      font-weight: bold;
      margin-bottom: 11px;
    }
    
    &__place {
      font-size: 14px;
      line-height: 16px;
      margin-bottom: 12px;
    }

    &__text {
      font-size: 16px;
      line-height: 19px;
      margin: 0;
    }

    &__btn {
      width: 150px;
      height: 50px;
      margin: 25px 0 3px;
      font-size: 16px;
      line-height: 22px;
      font-weight: bold;
      color: #118488;
      background-color: #fff;
      border: 1px solid #118488;
      border-radius: 4px;
      outline: none;
      cursor: pointer;
    }
}

.footer {
  display: flex;
  padding-left: 270px;
  align-items: center;
  width: 100%;
  height: 213px;
  font-size: 16px;
  line-height: 19px;
  box-shadow: 0 -1px 0 #e1e7ed;
    &__navigation {
      display: flex;
      justify-content: space-between;
      width: 685px;
    }
    &__li {
      list-style-type: none;
      margin-top: 9px;
        a {
          text-decoration: none;
          color: #3d4e61;
        }
    }
    &__li-margin {
      margin: 0 0 20px;
      font-weight: bold;
    }
    &__ul {
      padding: 0;
    }
}
